//    

body {
  font-family: 'Roboto', sans-serif;
}

//  

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

//    
a {
  color: inherit;
  text-decoration: none;
}

//    
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

//       
img {
  display: block;
  max-width: 100%;
  height: auto;
}
