.modal-film__play-btn {
  cursor: pointer;
     width: 55px;
     height: 40px;
     background: rgba(255, 24, 7, 0.733);
     border-radius: 7px;
     position: absolute;
     border: none;
     transition: all .4s cubic-bezier(.55,.055,.675,.19);
 
     &:after {
       content: "";
       position: absolute;
       left: 50%;
       top: 50%;
       transform: translateX(-33%) translateY(-50%);
       transform-origin: center;
       width: 0;
       height: 0;
       border-top: 10px solid transparent;
       border-bottom: 10px solid transparent;
       border-left: 16px solid #fff;
       z-index: 100;
 }
 
 &:hover,
 &:focus{
  box-shadow: 0px 4px 16px rgba(231, 68, 56, 0.651);
 }
 }
 
 
 .backdrop-video {
   overflow-y: auto;
   z-index: 1001;
  
   display: flex;
   justify-content: center;
   position: fixed;
   width: 100vw;
   height: 100vh;
   top: 0;
   padding: 20px;
  
   background: rgba(0, 0, 0, 0.56);
   transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
     visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
   &.modal-trailer--is-hidden {
     opacity: 0;
     visibility: hidden;
     pointer-events: none;
     & .modal-video__container {
       transform: translate(-50%, -50%) scale(2);
     }
   }
 
   @media screen and (min-width: 767px) and (min-height: 500px) {
     align-items: center;
   }
 }
 
 
 .modal-video__container {
   background: currentColor;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: scale(1) translate(-50%, -50%);
    width: 260px;
   height: 220px;
   z-index: 1001;
   transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
 
   @media screen and (min-width: 767px) {
  
    width: 630px;
    height: 330px;
 
   }
   @media screen and (min-width: 1280px) {
    width: 1064px;
    height: 717px;
   }
 }
 
 .modal__close-btn {
   display: flex;
   align-items: center;
   justify-content: center;
  
   border: transparent;
   position: absolute;
   width: 30px;
   height: 30px;
 
    top: -30px;
    right: -30px;
    z-index: 1001;
    border-radius: 50%;
    transition: background-color $trans-time $cubic, color $trans-time $cubic;
  
   &:hover {
    color: #fff;
     background-color: $accent-hover-color;
   }
 }
 
 
 
 .modal-video__trailer {
 
   position: fixed;
    bottom: 0;
    width: 100%;
    height: 100%;
 
 }

 .modal-video__error{
  position: absolute;
  font-weight: 700;
 
  color: rgba(252, 29, 13, 0.651);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  @media screen and (min-width: 768px) {
      font-size: 24px;
  }
  
  @media screen and (min-width: 1280px) {
    font-size: 32px;
  }
}
